    align-items: center;
    /* padding: 5px; */
}

.navbar_Items > .navbar_Item > p {
    /* text-decoration: underline; */
    cursor: pointer;
    color: white;
}

.link {
    color: white;
    text-decoration: none;
}



.container-slider {
    max-width: 1000px;
    height: 500px;
    margin: 20px auto 0;
    position: relative;
    overflow: hidden;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
    border-radius: 20px;
  }
  @media screen and (max-width: 700px){
    .container-slider {
      margin: 100px 10px 0;
    }
  }
  .slide {
    width: 100%;
    height: 100%;
    position: absolute;
    opacity: 0;
    transition: opacity ease-in-out 0.4s;
  }
  .slide img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .active-anim {
    opacity: 1;
  }
  
  .btn-slide {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background: #f1f1f1;
    border: 1px solid rgba(34, 34, 34, 0.287);
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
  
  .btn-slide img {
    width: 25px;
    height: 25px;
    pointer-events: none;
  }
  .prev {
    top: 50%;
    left: 20px;
    -webkit-transform: translateY(-60%);
            transform: translateY(-60%);
  }
  .next {
    top: 50%;
    right: 20px;
    -webkit-transform: translateY(-60%);
            transform: translateY(-60%);
  }
  
  
  .container-dots {
    position: absolute;
    bottom: 10px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    display: flex;
  }
  .dot {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 3px solid #f1f1f1;
    margin: 0 5px;
    background: #f1f1f1;
  }
  .dot.active {
    background: rgb(32, 32, 32);
  }

  .container-slider > img {
    position: inherit;
    top: 10px;
    left: 700px;
    direction: rtl;
    border: 1px white solid;
    border-radius: 10px;
    width: 100px;
    height: 20px;
    background-color: white;
    margin-top: 20px;
    margin-left: 20px;
    display: inline;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  }

  .container-slider > img:hover {
    border: 2px solid rgb(28, 28, 255);
    cursor: pointer;
  }

  .container-slider > .button {
    top: 445px;
    left: 20px;
  }


